}
body {
  font-family: PingFang SC, Hiragino Sans GB, Microsoft YaHei, Arial, sans-serif;
  font-size: 14px;
  line-height: 2;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: none;
  position: relative;
  max-width: 700px;
  margin: 0 auto;
  padding: 0;
  background: #fff;
  color: #555;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 400;
}
a {
  text-decoration: none;
  color: #08c;
  overflow-wrap: break-word;
  word-break: break-all;
}
.header {
  margin: 50px auto;
  text-align: center;
}
.header a {
  font-size: 16px;
  letter-spacing: 5px;
  text-transform: uppercase;
  color: #555;
}
.search-bar {
  display: block;
  margin: 5px auto 0;
  width: 150px;
  font-family: PingFang SC, Hiragino Sans GB, Microsoft YaHei, Arial, sans-serif;
  text-align: center;
  border: none;
  background-color: #fff;
}
.search-bar:active,
.search-bar:focus,
.search-bar:hover {
  outline: none;
  background-color: #fafafa;
  text-decoration: none;
}
.footer {
  margin-bottom: 15px;
  text-align: center;
  color: #888;
}
.footer a {
  color: #555;
}
.list-view {
  margin: 0 20px;
}
.list-view ol,
.list-view ul {
  padding: 0;
  list-style: none;
}
.list-item {
  position: relative;
  margin-bottom: 50px;
}
.item-title {
  display: inline-block;
  margin-bottom: 10px;
  font-size: 16px;
  color: #555;
}
.item-title:hover {
  color: #08c;
}
.item-date {
  display: inline-block;
  font-size: 12px;
  color: #888;
  border-top: 1px solid #ddd;
  padding-top: 12px;
}
.post-view {
  margin: 0 20px 50px;
}
.post-view a:hover {
  border-bottom: 1px dashed #08c;
}
.post-title {
  line-height: 1;
  margin-bottom: 50px;
}
@media (max-width: 767px) {
.post-title {
    font-size: 1.5em;
}
}
.post-date {
  float: right;
  font-size: 12px;
  font-weight: 400;
  line-height: 2;
  color: #888;
}
pre {
  background: #f7f7f7!important;
  padding: 16px;
  overflow-x: auto;
  white-space: pre;
}
code {
  background: #f7f7f7;
  font-family: Ubuntu Mono, PT Mono, Consolas, Monaco, Menlo, monospace !important;
}
blockquote {
  margin: 0;
  padding: 0 16px;
  border-left: 4px solid #ddd;
}
#app {
  max-width: 700px;
  margin: auto;
}



